.board {
    width: 100%;
    background-color: #1C1D1E;
    padding: 260px 0 150px 0;
    @media (max-width: $md-1) {
        padding: 77px 0 138px 0;
    }
    @media (max-width: $md-4) {
        padding: 32px 0;
    }
    // .board__container

    &__container {
    }

    // .board__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
        font-family: "Apercu", sans-serif;
        @media (max-width: $md-1) {
           display: flex;
           flex-direction: column;
           align-items: center;
        }
        @media (max-width: $md-4) {
           display: block;
         }
    }

    &__header {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        margin: 0 0 98px 0;
        @media (max-width: $md-4) {
            margin: 0 0 50px 0;
        }
    }

    // .board__title

    &__title {
        font-size: 40px;
        font-weight: 700;
        position: relative;
        @media (max-width: $md-3) {
            font-size: 25px;
        }  
       
        &::before {
            position: absolute;
            content: "";
            display: block;
            left: 0;
            bottom: -15px;
            width: 80px;
            height: 3px;
            background-color: $greyColor;
        }
    }

    // .board__content

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        @media (max-width: $md-1) {
            flex-direction: row;
        }
        @media (max-width: $md-4) {
            display: none;
        }
    }

    // .board__row

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 0 80px 0;
        @media (max-width: $md-1) {
            flex-direction: column;
            justify-content: center;
            margin: 0 40px;
        }
        @media (max-width: $md-2) {
            margin: 0 10px;
        } 
    }

    // .board__item

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 12px;
        @media (max-width: $md-1) {
            margin: 40px 0;
        }
        @media (max-width: $md-4) {
            width: 50%;
            align-items: flex-start;
        }
    }

    // .board__image

    &__image {
        width: 440px;
        height: 440px;
        object-fit: cover;
        margin: 0 0 30px 0;
        @media (max-width: $md-3) {
            width: 353px;
            height: 321px;
        }   
        @media (max-width: $md-4) {
            width: 90%;
            margin: 0 0 17px 0;
        }
        @media (max-width: $md-5) {
           height: 130px;
        }
    }

    // .board__name

    &__name {
        font-size: 30px;
        font-weight: 700;
        margin: 0 0 17px 0;
        @media (max-width: $md-3) {
            font-size: 20px;
        }   
        @media (max-width: $md-4) {
            margin: 0 0 11px 0;
        }
        @media (max-width: $md-5) {
            width: 98%;
         }
    }

    // .board__info

    &__info {
        color: $greyColor;
        font-size: 22px;
        font-weight: 500;
        @media (max-width: $md-3) {
            font-size: 16px;
        }   
        @media (max-width: $md-5) {
            width: 98%;
         }
    }

}

.board-slider {
    position: relative;
    display: none;
    @media (max-width: $md-4) {
        display: block;
    }
    
}
.board-slider .slick-slide{
    display: flex;
    align-items: center;
}
.board-slider .slick-dots{
    margin: 25px 30px 0 0;
} 
.board-slider .slick-dots li{
    margin: 0 10px;
} 
