.delivery {
    width: 100%;
    background-color: $mainColor;
    color: #fff;
    padding: 100px 0;
    // .delivery__container

    &__container {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    // .delivery__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-family: "Apercu", sans-serif;
        @media (max-width: 650px) {
            flex-direction: column;
            justify-content: center;
        } 
    }

    // .delivery__content

    &__content {
        display: flex;
        flex-direction: column;
        margin: 0 20px 0 0 ; 
        max-width: 530px;
        @media (max-width: $md-2) {
            max-width: 400px;
            margin: 0;
        } 
        @media (max-width: 650px) {
           max-width: 100%;
           margin: 0 0 76px 0;
           align-items: flex-start;
        } 
    }

    // .delivery__title

    &__title {
         font-size: 40px;
         font-weight: 700;
         margin: 0 0 58px 0;
         position: relative;
         width: 530px;
         @media (max-width: $md-2) {
            font-size: 30px;
            margin: 0 0 40px 0;
            width: auto;
        }
        @media (max-width: $md-4) {
            font-size: 25px;
            margin: 0 0 58px 0;
        }
        @media (max-width: 650px) {
            font-size: 30px;
        } 
         &::after {
            position: absolute;
            display: block;
            content: "";
            left: 0;
            bottom: -15px;
            width: 80px;
            height: 3px;
            background-color: $greyColor;
         }
    }

    // .delivery__text

    &__text {
        color: $greyColor;
        font-size: 22px;
        margin: 0 0 58px 0;
        @media (max-width: $md-2) {
            font-size: 18px;
            margin: 0 0 40px 0;
            max-width: 350px;
        }
        @media (max-width: $md-4) {
            font-size: 16px;
        }
        @media (max-width: 650px) {
            max-width: 100%;
            font-size: 20px;
        } 
    }

    // .delivery__toggle

    &__button {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: transparent;
        border: 1px solid #454646;
        background-color: #333333;
        cursor: pointer;
        position: relative;
        z-index: 1;
        &:hover {
            background-color: #1C1D1E;
            &::before {
                display: none;
            }
        }
        &::before {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.7);
            z-index: 3;
        }
        
    }

    &__toggle {
        width: 400px;
        height: 50px;
        display: flex;
        align-items: center;
        @media (max-width: $md-3) {
            width: 280px;
        }
        @media (max-width: $md-4) {
            width: 333px;
        }
        @media (max-width: $md-6) {
            width: 285px;
        }
    }

    // .delivery__left

    &__left {
        border-bottom-left-radius: 4px;
        border-top-left-radius: 4px;
    }
    &__left.active {
        border: 1px solid $greyColor;
        &:hover {
            background-color: #333333;
        }
        &::before {
            display: none;  
        }
    }

    // .delivery__country

    &__country {
        margin: 0 10px 0 0;
        width: 28px;
        height: 20px;
        border-radius: 3px;
        @include bg("contain");
    }

    // .delivery__value

    &__value {
        font-size: 20px;
    }

    // .delivery__right

    &__right {
        border-bottom-right-radius: 4px;
        border-top-right-radius: 4px;
    }
    &__right.active {
        border: 1px solid $greyColor;
        &:hover {
            background-color: #333333;
        }
        &::before {
            display: none;  
        }
    }

    // .delivery__map

    &__map {
        display: none;
       
        &_1 {
            width: 1024px;
            @media (max-width: 1500px) {
                width: 800px;
            }
            @media (max-width: 1230px) {
                width: 600px;
            }
            @media (max-width: $md-2 + 30px) {
                width: 490px;
            }
            @media (max-width: 900px) {
                width: 400px;
            }
            @media (max-width: $md-4) {
                width: 336px;
            }
            @media (max-width: 650px) {
                width: 100%;
            } 
        }
        &_1.active {
            display: block;
        }
        &_2 {
            @media (max-width: $md-2) {
                width: 390px;
            }
            @media (max-width: 850px) {
               width: 300px;
            } 
            @media (max-width: 650px) {
                width: 100%;
            } 
        }
        &_2.active {
            display: block;
        }
    }
}
