.safety {
    width: 100%;
    position: relative;
    height: 100vh;
    @media (max-width: $md-4) {
        background-color: #1C1D1E;
        height: auto;
    }
    
    // .safety__container

    &__container {
        width: 100%;
        height: 100%;
        @media (max-width: $md-4) {
            display: flex;
            flex-direction: column;
        }
    }

    // .safety__body

    &__body {
        padding: 95px 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        font-family: "Apercu", sans-serif;
        color: #fff;
        @media (max-width: $md-2) {
            padding: 56px 0;
        }
        @media (max-width: $md-4) {
            padding: 44px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    // .safety__title

    &__title {
        font-size: 50px;
        font-weight: 700;
        position: relative;
        margin: 0 0 48px 0;
        @media (max-width: $md-2) {
            font-size: 40px;
        }
        @media (max-width: $md-4) {
           font-size: 30px;
           &::after {
            position: absolute;
            content: "";
            display: block;
            width: 80px;
            height: 3px;
            background-color: $greyColor;
            left: 0;
            bottom: -15px;
            }
        }
        @media (max-width: $md-6) {
           font-size: 22px;
        }
    }

    // .safety__content

    &__content {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        position: relative;
        @media (max-width: $md-4) {
            flex-direction: column;

        }
    }

    // .safety__text
 
    &__text {
        max-width: 448px;
        font-size: 22px; 
        margin: 0 30px;
        @media (max-width: $md-2) {
            font-size: 16px;
            max-width: 326px;
        }
        @media (max-width: $md-4) {
           max-width: 100%;
           color: $greyColor;
           font-size: 20px;
           margin: 0 0 20px 0;
        }
        @media (max-width: $md-6) {
            font-size: 16px;
         }
    }
 
    // .safety__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -50;
        object-fit: cover;
        @media (max-width: $md-4) {
            position: relative;
            width: 100%;
            z-index: 1;
            object-fit: contain;
        }
    }
}
