.work {
    width: 100%;
    background-color: #1C1D1E;
    padding: 123px 0;
    @media (max-width: $md-4) {
        padding: 30px 0;
    }
    // .work__container

    &__container {
    }

    // .work__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
        font-family: "Apercu", sans-serif;
    }

    // .work__title

    &__title {
        font-weight: 700;
        font-size: 40px;
        margin: 0 0 65px 0;
        position: relative;
        @media (max-width: $md-4) {
            font-size: 25px;
        }
        &::after {
            display: block;
            position: absolute;
            content: "";
            width: 80px;
            height: 3px;
            background-color: $greyColor;
            left: 0;
            bottom: -15px;
        }
    }

    // .work__content

    &__content {
        display: block;
        @media (max-width: $md-4) {
            display: none;
        }
    }

    &__group {
        position: relative;
        width: 100%;
        height: auto;
    }

    &__height {
        position: absolute;
        width: 95px;
        height: 100%;
        left: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            position: absolute;
            display: block;
            content: "";
            width: 3px;
            height: 100%;
            background-color: #3A3A33;
            right: 7px;
            bottom: 2px;
        }
    }

    &__distance {
        color: $greyColor;
        font-size: 18px;
        font-weight: 500;
    }

    &__ico {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 17px;
        height: 16px;
        @include bg("contain");
        z-index: 2;
    }
}

.work-slider {
    position: relative;
    display: none;
    @media (max-width: $md-4) {
        display: block;
    }
}
.work-slider .slick-dots {
    margin: 25px 30px 0 0;
}
.work-slider .slick-dots li {
    margin: 0 7px;
}



.work-item {
    position: relative;
    height: 125px;
    width: 100%;
    margin: 0 0 20px 0;
    @media (max-width: $md-1) {
        height: 230px;
    }
    // @media (max-width: $md-6) {
    //     height: 270px;
    // }
    // .work-item__height

    &__height {
        position: absolute;
        width: 95px;
        height: 100%;
        left: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
        &::before {
            position: absolute;
            display: block;
            content: "";
            width: 3px;
            height: 100%;
            background-color: #3d3d3d;
            right: 7px;
            bottom: 2px;
        }
    }

    // .work-item__distance

    &__distance {
        color: $greyColor;
        font-size: 18px;
        font-weight: 500;
    }

    // .work-item__ico

    &__ico {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 17px;
        height: 16px;
        @include bg("contain");
        z-index: 2;
    }

    // .work-item__main

    &__main {
        height: 100%;
        position: relative;
        margin: 0 0 0 120px;
        @media (max-width: $md-4) {
            margin: 0 15px;
        }
        @media (max-width: $md-6) {
            margin: 5px;
        }
    }

    // .work-item__info

    &__info {
        position: relative;
        width: 680px;
        padding: 25px 20px 25px 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #292929;
        border-radius: 5px;
        color: $greyColor;
        @media (max-width: $md-1) {
            width: 100%;
           &::after {
                position: absolute;
                display: block;
                content: "";
                width: 10px;
                height: 115px;
                left: 0;
                bottom: -110px;
                background-color: #292929;
                border-radius: 10px;
           }
        }
    }

    &__info-block {
        display: flex;
        align-items: center;
    }

    // .work-item__number

    &__number {
        font-weight: 700;
        font-size: 50px;
        margin: 0 21px 0 0;
    }

    // .work-item__text

    &__text {
        max-width: 392px;
        font-size: 18px;
        font-weight: 400;
        @media (max-width: $md-1) {
            font-size: 16px;
            max-width: 350px;
        }
        @media (max-width: $md-4) {
            font-size: 14px;
        }
    }

    // .work-item__example

    &__example {
        z-index: 2;
        width: 100%;
        background-color: #292929;
        position: relative;
        border-radius: 10px;
        height: 10px;
        bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        @media (max-width: $md-1) {
            bottom: -105px;
            justify-content: flex-start;
            padding-left: 150px;
        }
    }

    // .work-item__image

    &__image {
        position: relative;
        bottom: 15px;
        @media (max-width: $md-5) {
            max-width: 120px;
        }
    }

    &__mode {
        display: flex;
        align-items: center;
    }

    &__mode-ico {
        @include bg("contain");
        width: 39px;
        height: 27px;
        margin: 0 5px 0 0;
    }

    &__mode-text {
        font-size: 12px;
    }
   
}

.work-item-1 {

    // .work-item-1__truck

    &__truck {
        left: -100px;
        @media (max-width: 600px) {
            left: -130px;
        }
        @media (max-width: $md-5) {
            bottom: 10px;
        }
    }

    // .work-item-1__arrow

    &__arrow {
        bottom: 0;
        left: -90px;
        @media (max-width: 600px) {
            left: -130px;
        }
    }

    // .work-item-1__storage

    &__storage {
        bottom: 20px;
        @media (max-width: 600px) {
            left: -100px;
        }
        @media (max-width: $md-5) {
            left: -130px;
            bottom: 15px;
        }
    }
}
