        }
        @media (max-width: $md-5) {
            left: -90px;
        }
    }

    // .work-item-2__signal

    &__signal {
        bottom: 35px;
        left: -60px;
        @media (max-width: 600px) {
            left: -110px;
        }
        @media (max-width: $md-5) {
            display: none;
        }
    }
