.header {
    position: absolute;
    width: 100%;
    height: 81px;
    top: 0;
    left: 0;
    z-index: 50;
    @media (max-width: 1440px) {
        height: 61px;
    }
    @media (max-width: $md-5) {
        height: 51px;
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $mainColor;
        z-index: 2;
    }
    // .header__container

    &__container {
        width: 100%;
        height: 100%;
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__logo

    &__logo {
        @include bg("contain");
        width: 110px;
        height: 20px;
        cursor: pointer;
        z-index: 3;
        position: relative;
    }

    // .header__burger

    &__burger.active {
        span {
            display: none;
        }
        &::before {
            transform: rotate(-45deg);
            top: 9px;
        }
        &::after {
            transform: rotate(45deg);
            bottom: 7px;
        }
    }
    &__burger {
        display: none;
        @media (max-width: $md-5) {
            display: block;
            position: relative;
            width: 29px;
            height: 18px;
            z-index: 3;
            &::before,
            &::after {
                content: "";
                position: absolute;
                background-color: $greyColor;
                width: 100%;
                height: 2px;
                left: 0;
                transition: all .3s ease 0s;
            }
            &::before {
                top: 0;
            }
            &::after {
                bottom: 0;
            }
            span {
                position: absolute;
                width: 100%;
                background-color: $greyColor;
                height: 2px;
                left: 0;
                top: 8px;
            }
        }
        
    }

    // .header__menu

    &__menu.active {
        right: 0;
    }
    &__menu {
        transition: all .4s ease 0s;
        z-index: 3;
        position: relative;
        @media (max-width: $md-5) {
            position: fixed;
            top: 0;
            right: -100%;
            width: 50%;
            height: 100%;
            background-color: $mainColor;
            z-index: 1;
            padding: 80px 20px;
        }
       
    }

    // .header__items

    &__items {
        display: flex;
        align-items: center;
        @media (max-width: $md-5) {
           flex-direction: column;
           align-items: flex-end;
        }
    }

    // .header__item

    &__item {
        list-style: none;
        margin: 0 35px;
        padding-bottom: 3px;
        border-bottom: 2px solid rgba(0, 0, 0, 0);
        &:hover, &:focus {
            border-bottom: 2px solid $greyColor;
        }
        @media (max-width: $md-4) {
            margin: 0 15px;
        }
        @media (max-width: $md-5) {
           margin: 0 0 40px 0;
         }
        a {
            text-decoration: none;
            color: #fff;
            font-family: "Apercu";
            font-weight: 500;
            font-size: 17px;
            cursor: pointer;
            position: relative;
        }
    }
}

