*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}
 
// Vars
$mainColor: #0F1011;
$greyColor: #B5B7B7;

$container: 1400px + 30px;
$md-1: $container;
$md-2: 1030px;
$md-3: 992px;
$md-4: 767px;
$md-5: 500px;
$md-6: 400px;

// Components
.container {
	height: 100%;
	max-width: $container;
	margin: 0 auto;
	padding: 0 15px;
}

.body.lock {
	overflow: hidden;
}