.advantages {
    width: 100%;
    height: 930px;
    background-color: $mainColor;
    padding: 50px 0;
    overflow: hidden;
    @media (max-width: 1300px) {
        height: 900px;
    }
    @media (max-width: $md-2) {
        height: 750px;
    }
    @media (max-width: 880px) {
        height: 650px;
    }
    @media (max-width: $md-4) {
            height: auto;
    }
    @media (max-width: $md-6) {
        padding: 80px 0;
    }
    // .advantages__container

    &__container {
        max-width: $container + 200px;
        height: 100%;
        margin: 0;
        padding: 0;
        position: relative;
        margin: 0 0 400px 0;
    
    }

    // .advantages__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
        font-family: "Apercu", sans-serif;
        padding: 0 15px 0 15px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        @media (max-width: $md-4) {
            justify-content: flex-start;

        }
    }

    // .advantages__title

    &__title {
        font-weight: 700;
        font-size: 40px;
        margin: 0 0 76px 50px;
        position: relative;
        @media (max-width: $md-2) {
            margin: 0 0 68px 25px;
        }
        @media (max-width: $md-4) {
            margin: 0 0 76px 0;
        }
        &::after {
            position: absolute;
            display: block;
            content: "";
            background-color: $greyColor;
            left: 0;
            bottom: -15px;
            width: 80px;
            height: 3px;
        }
    }

    // .advantages__content

    &__items {
        display: flex;
        align-items: flex-start;
        @media (max-width: $md-4) {
            flex-direction: column;
        }
    }

    // .advantages__row

    &__row {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin: 0 30px;
        @media (max-width: $md-4) {
            margin: 0;
        }
    }

    // .advantages__item

    &__item {
        display: flex;
        align-items: center;
        margin: 0 0 50px 0;
        @media (max-width: $md-2) {
            margin: 0 0 25px 0;
        }
    }
 
    // .advantages__ico

    &__ico {
        width: 26px;
        height: 33px;
        @include bg("contain");
        margin: 0 10px 0 0;
        @media (max-width: $md-4) {
            margin: 0 16px 0 0;
        }
    }

    // .advantages__text

    &__text {
        color: $greyColor;
        font-weight: 500;
        font-size: 22px;
        @media (max-width: $md-2) {
            font-size: 18px;
        }
    }

    // .advantages__image

    &__image {
        position: absolute;
        left: 0;
        bottom: 0;
        @media (max-width: 1600px) {
            left: -250px;
        }
        @media (max-width: 1300px) {
            left: -340px;
            height: 730px;
        }
        @media (max-width: $md-2) {
            height: 630px;
        }
        @media (max-width: 880px) {
            height: 530px;
        }
        @media (max-width: $md-4) {
            height: 400px;
            left: 0;
            bottom: -400px;
        }
        @media (max-width: $md-6) {
            height: 400px;
            left: -100px;
            bottom: -400px;
        }
    }
}
