.self {
    width: 100%;
    background-color: $mainColor;
    color: #fff;
    overflow: hidden;
    padding:40px 0;
    // .self__body

    &__body {
        width: 100%;
        height: 100%;
        font-family: "Apercu", sans-serif;
        padding: 0 0 0 260px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        @media (max-width: $md-1) {
            padding: 0 0 0 100px;
        }
        @media (max-width: $md-2) {
            padding: 0 0 0 20px;
        }
        @media (max-width: $md-5) {
            flex-direction: column;
            align-items: flex-end;
         }
    }

    // .self__content

    &__content {
        display: flex;
        flex-direction: column;
    }

    // .self__caption

    &__caption {
        font-size: 40px;
        font-weight: 700;
        margin: 0 0 50px 0;
        position: relative;
        width: 452px;
        @media (max-width: $md-2) {
            font-size: 30px;
            width: 400px;
        }
        @media (max-width: $md-4) {
            width: 330px;
        }
        @media (max-width: $md-5) {
            font-size: 22px;
            width: 100%;
            padding: 0 15px;
         }
       
        &::after {
            display: block;
            position: absolute;
            content: "";
            bottom: -15px;
            left: 0;
            width: 80px;
            height: 3px;
            background-color: $greyColor;
            @media (max-width: $md-5) {
                left: 15px;
             }
        }
    }

    // .self__text

    &__text {
        color: $greyColor;
        font-size: 22px;
        @media (max-width: $md-2) {
            font-size: 18px;
            width: 400px;
        }
        @media (max-width: $md-4) {
            width: 330px;
        }
        @media (max-width: $md-5) {
            font-size: 16px;
            width: 100%;
            padding: 0 15px;
         }
    }

    // .self__image

    &__image {
        position: relative;
        object-fit: contain;
        @media (max-width: 1440px) {
            right: -20px;
        }
        @media (max-width: $md-2) {
            right: 40px;
            width: 700px;
        }
        @media (max-width: $md-4) {
           width: 400px;
           right: 0px;
        }
        @media (max-width: 600px) {
            width: 300px;
            right: 20px;
         }
         @media (max-width: $md-5) {
            max-width: 100%;
            right: 0;
         }
    }
}
 