.footer {
    width: 100%;
    height: 110px;
    background-color: $mainColor;
    color: #fff;
    position: relative;
    // .footer__body

    &__body {
        width: 100%;
        height: 100%; 
        font-family: "Apercu", sans-serif;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column;
            justify-content: center;
        }
    }

    // .footer__main

    &__main {
        display: flex;
        align-items: center;
        @media (max-width: $md-3) {
            margin: 0 0 35px 0;
            justify-content: space-between;
            width: 100%;
        }
    }

    // .footer__logo

    &__logo {
        @include bg("contain");
        width: 113px;
        height: 20px;
        margin: 0 125px 0 0;
        @media (max-width: $md-1) {
            margin: 0 60px 0 0;
        }
        @media (max-width: $md-2) {
            margin: 0 40px 0 0;
        }
        @media (max-width: $md-5) {
            margin: 0 10px 0 0;
            width: 70px;
            height: 12px;
        }
    }
    

    // .footer__items

    &__items {
        display: flex;
        align-items: center;
    }

    // .footer__item

    &__item {
        list-style: none;
        margin: 0 40px 0 0;
        font-size: 16px;
        @media (max-width: $md-2) {
            margin: 0 30px 0 0;
        }
        @media (max-width: $md-5) {
            margin: 0 10px 0 0;
            
        }
        a {
            text-decoration: none;
            color: #fff;
            font-size: 16px;
            font-weight: 500;
            @media (max-width: $md-5) {
                font-size: 12px;
            }
        }
    }

    // .footer__other

    &__other {
        display: flex;
        align-items: center;
        @media (max-width: $md-3) {
            justify-content: space-between;
            width: 100%;
        }
    }

    // .footer__email

    &__email {
        font-size: 16px;
        font-weight: 500;
        margin: 0 100px 0 0 ;
        @media (max-width: $md-1) {
            margin: 0 70px 0 0;
        }
        @media (max-width: $md-2) {
            margin: 0 60px 0 0;
        }
        @media (max-width: $md-5) {
            margin: 0 10px 0 0;
            font-size: 10px;
        }
    }

    // .footer__copyright

    &__copyright {
        display: flex;
        align-items: center;
    }

    // .footer__c

    &__c {
        font-size: 14px;
        font-weight: 500;
        margin: 0 39px 0 0;
        @media (max-width: $md-5) {
            margin: 0 20px 0 0;
            font-size: 10px;
        }
    }

    // .footer__text

    &__text {
        font-size: 14px;
        font-weight: 500;
        @media (max-width: $md-5) {
            font-size: 10px;
        }
    }
}
 