.first {
    width: 100%;
    height: 100vh;
    position: relative;
    @media (max-height: 640px) and (max-widht: 830px) {
        height: 200vh;
    }
    
    &__container {
        width: 100%;
        height: 100%;
    }

    // .first__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        color: #fff;
        padding: 0 0 45px 0;
    }

    // .first__block

    &__block {
        display: flex;
        align-items: flex-end;
        font-family: "Apercu", sans-serif;
    }

    // .first__caption

    &__caption {
        font-size: 60px;
        font-weight: 700;
        max-width: 450px;
        margin: 0 48px 0 0;
        text-align: right;
        @media (max-width: $md-4) {
            font-size: 35px;
        }
    }

    // .first__down

    &__down {
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;
        @media (max-width: $md-5) {
            display: none;
        }
    }  

 

    // .first__ico

    &__ico {
        @include bg("contain");
        width: 14px;
        height: 20px;
        margin: 0 0 10px 0;
        animation: arrow-down 1s infinite ease;
    }

    @-webkit-keyframes arrow-down {
        0% {
            transform: translateY(0px);
        }
         50% {
            transform: translateY(3px);
        } 
        100% {
            transform: translateY(0px);
        }
    }
    @keyframes arrow-down  {
        0% {
            transform: translateY(0px);
        }
        50% {
            transform: translateY(3px);
        } 
        100% {
            transform: translateY(0px);
        }
    }

    // .first__text

    &__text {
        font-size: 12px;
        font-weight: 500;
        text-align: center;
    }

    // .first__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -50;
        width: 100%;
        height: 100%;
        object-fit: cover;
        &_2 {
            display: none;
            @media (max-width: $md-3) {
                display: block;
            }
        }
        &_3 {
            display: none;
            @media (max-width: $md-5) {
                display: block;
            }
        }
    }
}
